/* src/app/users/users.css */
.main {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.header {
  width: 95%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.spacer {
  flex: 1 1 auto;
}
.users {
  margin: 20px;
  padding: 20px;
  width: 80%;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  border: 1px solid #dbe3f8;
  border-radius: 24px;
}
.controls {
  width: 95%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
  margin-bottom: 40px;
  gap: 20px;
}
.search-input {
  width: 500px;
  align-self: flex-start;
}
.mat-form-field {
  height: 60px;
}
button {
  margin: 10px;
}
mat-expansion-panel {
  width: 100%;
  border-radius: 6px;
}
mat-expansion-panel-header {
  height: 80px;
}
@media (max-width: 768px) {
  .search-input {
    width: 350px;
  }
  .users {
    border-radius: 24px;
  }
  mat-expansion-panel {
    border-radius: 4px;
  }
}
/*# sourceMappingURL=users.css.map */
